.CpuLoadGraph {
  max-width: 1024px;
  margin-left: auto;
  margin-right: auto;
}
.CpuLoadGraph_settingsTitle {
  font-family: monospace;
}
.CpuLoadGraph_settings {
  display: flex;
  flex-wrap: wrap;
}
.CpuLoadGraph_settings .SwitchButton {
  margin-right: 15px;
}
.CpuLoadGraph_cpuLoadSetting {
  margin-top: 15px;
  width: 100%;
}
.CpuLoadGraph_cpuLoadSettingInput {
  border-radius: 10px;
  box-shadow: none;
  border: 3px solid #7ebdb4;
  padding: 3px;
}
.CpuLoadGraph_cpuLoadSettingLabel {
  font-family: monospace;
  margin-left: 12px;
}
.CpuLoadGraph_currentAverage {
  font-size: 1.5em;
  margin-top: 10px;
  margin-bottom: 10px;
}
.CpuLoadGraph_currentAverageValue {
  border-bottom: solid;
}
.CpuLoadGraph_mainBoard {
  font-family: monospace;
  padding: 9px;
  margin-top: 15px;
  margin-bottom: 25px;
}
.CpuLoadGraph_alertList {
  display: flex;
  flex-direction: row;
}
